#subheading {
  font-size: 24px;
  font-weight: 100;
  margin: 20px auto;
  max-width: 880px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.device_table {
  position: relative;
  left: 15%;
  margin-top: 30px;
  width: 434px;
}

.twitterIcon{
  right: 5px;
}

.siteTitle {
  height: 25px;
  margin-top: 21px;
  display: block;
}

.settingsMenuButton {
  margin-bottom: 16px;
  width: 100;
}

.settingsBtns {
  top: 1rem;
  margin-left: 20px;
}

.settingsComponents{
  width: 75%;
  margin-top: 80px;
}

.image-div{
  margin-top:-40px;
}

.colorPicker{
  margin-left: 2.8rem;
  margin-top: -1.8rem;
  width: 6rem;
}

.color-box{
  display: inline-block;
  width: 23px;
  height: 23px;
  border-radius: 3px;
  border: 1px solid #0000006e;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: -8px;
}

.Link a {
  color : #f75959;
  text-decoration : none;
}

.Link a:hover {
  color: #ff0000;
  transition: all 0.2s ease-in-out;
}

.setting-avatar{
  width: 150px;
  border-radius: 6px;
  display: block;
  height: 150px;
  margin: 1rem 0;
}

.close-avatar{
  float: right;
  border-radius: 50%;
  border: 1px solid #999999;
  position: relative;
  top: -9px;
  height: 25px;
  width: 25px;
  right: 20px;
  background-color: white;
  cursor: pointer;
}

.avatar-empty-box{
  border: 1px solid #CED4DA;
  height: 148px;
  border-radius: 6px;
  width: 148px;
  margin: 1rem 0;
}

.avatar-add-button{
  width: 40px;
  display: block;
}

.avatar-empty-box:hover {
  background-color: #CED4DA;
  cursor: pointer;
}

.img-upld {
  width: 40%;
  padding: 0px;
}

.rightMenu {
  width: 64%;
  background-color: #fff;
}

.right-chevron{
  margin-top: 10px;
  position: absolute;
  right: 10px;
}

.email-field:hover{
  cursor: not-allowed;
}

.time-zone-picker > ul {
  background-color: #fff;
}

@media only screen and (max-width: 1060px){
  .settings-list-dropdown{
    display: block;
  }

  .settings-list{
    display: none;
  }

  .leftMenu {
    width: 100%;
    height: auto;
  }

  .rightMenu {
    height: auto;
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .reduceSettingDiv {
    width: 70%;
  }

  .settingsBtns {
    margin-left: 0rem;
  }

  .img-upld {
    padding: 25px;
    margin-left: 5%;
  }
}
